<template>

  <full-height-layout>
    <div slot="layout_title">菜品</div>

    <freight-view-top slot="layout_inner_head" :freights="freightList"></freight-view-top>

    <freight-view-body
      :freights="freightList"
      slot="layout_inner_body"
      v-if="freightList.length > 0"
    ></freight-view-body>
    <empty v-else slot="layout_inner_body"></empty>

    <freight-view-footer slot="layout_footer" :_pageLink="pageLink" @_loadData_Father="loadData"></freight-view-footer>

    <div slot="layout_extra">
      <freight-view-search @loadData_Father="loadData"></freight-view-search>
    </div>
  </full-height-layout>

</template>

<script>
import tool from '@/common/es/tool'
import conn_util from '@/common/es/conn_util'

import FullHeightLayout from '@/components/Layout/FullHeightLayout.vue'
import FreightViewTop from './Top/FreightViewTop.vue'

import conn from '@/service/conn'
import FreightViewBody from './Body/FreightViewBody.vue'
import FreightViewSearch from './Filter/FreightViewSearch.vue'
import FreightViewFooter from './Footer/FreightViewFooter.vue'
import Empty from '@/components/Shimmer/Empty.vue'

export default {
  components: { FullHeightLayout, FreightViewTop, FreightViewBody, FreightViewSearch, FreightViewFooter, Empty },
  name: 'FreightView',
  data() {
    return {
      freightList: [],
      pageLink: {
        next: '',
        previous: '',
        count: 0,
        offsetNow: 0,
        limit: 100
      }
    }
  },
  mounted() {
    this.loadData()
  },
  methods: {
    loadData(url, condition = {}) {
      if (url == null) {
        url = conn_util.buildFirstPagerUrl(conn.freight.url, condition, { offset: 0, limit: this.pageLink.limit })
      }
      
      conn.freight.getListPage(url).then(res => {
        
        this.freightList = res.results
        this.pageLink = conn_util.buildPageLink(url, res.count, 100, res.next, res.previous)
      })
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/listing.sass"

</style>


